.container {
  margin: auto;
  padding: 50px 0 73px;
  max-width: $container-max-width;
  display: flex;
  flex-wrap: wrap;
  color: $main-text-color;
}

.product-card {
  margin: 0 $product-card-margin-right 46px 0;
  width: $product-card-width;
  border: $card-border solid #f3f3f3;
  border-radius: 5px;

  &:nth-child(4n) {
    margin-right: 0;
  }

  &:nth-child(n + 5) {
    margin-bottom: 0;
  }
}

.photo {
  display: flex;
  padding: 32px 19px 40px;

  &__card {
    width: 160px;
    height: 134px;
  }
}

.product-information {
  display: flex;
  flex-direction: column;
  padding: 0 16px;

  &__name {
    padding-bottom: 4px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
  }

  &__code {
    padding-bottom: 16px;
    font-size: 10px;
    line-height: 14px;
    color: #616070;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    padding-bottom: 24px;

    .stars {
      display: none;

      &__star {
        height: 16px;
        width: 16px;
        margin-right: 4px;
        background-image: url(../images/star.svg);
        background-position: center;
        background-repeat: no-repeat;
      }
    }

    .stars--active {
      display: flex;
    }

    .stars--5 .stars__star,
    .stars--1 .stars__star:nth-child(-n + 1),
    .stars--2 .stars__star:nth-child(-n + 2),
    .stars--3 .stars__star:nth-child(-n + 3),
    .stars--4 .stars__star:nth-child(-n + 4) {
      background-image: url(../images/star-active.svg);
    }

    .stars .stars__star:last-child {
      margin-right: 0;
    }

    .comment {
      display: flex;
      align-items: center;
      font-size: 10px;
      line-height: 14px;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;

    .price {
      font-size: 12px;
      line-height: 18px;
      color: #616070;
    }

    .cash {
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
    }
  }
}

.product-buy {
  display: flex;
  justify-content: center;
  padding: 16px ;

  &__button {
    width: 100%;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    text-transform: uppercase;
    color: $button-color;
    background: $blue-color;
    border-radius: 5px;


    &:hover {
      box-sizing: border-box;
      color: $blue-color;
      background: $button-color;
      border: 1px solid $blue-color;
    }
  }
}
